.info-container {
  width: 100%;
  height: 300px;
  background-color: rgb(255, 255, 255);
  color: rgb(0, 0, 0);
  margin-bottom: 100vh;
  /* background:rgb(126, 126, 126) */
}

.info {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;

}

@media only screen and (max-width: 960px) {
  .info-container {
    height: 200px;
    width: 100%;
  }

  .info > h1 {
    text-align: center;
    font-size: 25px;
  }
}
